'use client';
import React from 'react';
import { propertiesData } from '../../../data/listings';
import { FiMapPin, FiHome, FiGrid, FiDollarSign } from 'react-icons/fi';

const PropertyDetailsPage = ({ params }) => {
  const property = propertiesData.find(p => p.id.toString() === params.id);

  if (!property) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-black text-white">
        <p className="text-2xl">Property not found.</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black text-white py-24 px-4 sm:px-6 lg:px-8">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white/10 rounded-2xl shadow-2xl overflow-hidden">
          <img src={property.image} alt={property.name} className="w-full h-96 object-cover" />
          <div className="p-8">
            <div className="flex justify-between items-start mb-4">
              <h1 className="text-4xl font-bold text-teal-400">{property.name}</h1>
              <p className={`text-lg font-semibold px-4 py-1 rounded-full ${property.status === 'Active' ? 'bg-green-500/20 text-green-300' : 'bg-red-500/20 text-red-300'}`}>
                {property.status}
              </p>
            </div>
            <p className="text-gray-300 text-lg mb-6">{property.description}</p>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-lg">
              <div className="flex items-center gap-3">
                <FiDollarSign className="text-teal-400" size={24} />
                <span className="font-semibold">Price:</span>
                <span>{property.price}</span>
              </div>
              <div className="flex items-center gap-3">
                <FiHome className="text-teal-400" size={24} />
                <span className="font-semibold">Type:</span>
                <span>{property.type}</span>
              </div>
              <div className="flex items-center gap-3">
                <FiMapPin className="text-teal-400" size={24} />
                <span className="font-semibold">Location:</span>
                <span>{property.location}</span>
              </div>
              <div className="flex items-center gap-3">
                <FiGrid className="text-teal-400" size={24} />
                <span className="font-semibold">Bedrooms:</span>
                <span>{property.bedrooms}</span>
              </div>
              <div className="flex items-center gap-3">
                <FiGrid className="text-teal-400" size={24} />
                <span className="font-semibold">Land Size:</span>
                <span>{property.landSize}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PropertyDetailsPage;